export default {
  name: 'more',
  props: {
    moreTxt:Object,
  },
  data: () => ({
      stopBtn  : false,   // 오늘하루 보지 않기 버튼
      stopCheck: [],      // 버튼 체크
    }),
  methods: {
    // 모달창 닫기 버튼
    closeBtn(){
      this.$emit('close')
    },
    // 모달 끄는 동시에 동의 체크
    agreeBtn(){
      
      switch (this.moreTxt.agree) {
        case 'agree1':
          this.$emit('agree1')
          break;

        case 'agree2':
          this.$emit('agree2')
          break;

        case 'agree3':
          this.$emit('agree3')
          break;

        case 'agree4':
          this.$emit('agree4')
          break;

        default:
          console('값 없음')
          break;
      }
    }
  }
}
